//  
const cookieUtil = {
  /**
   *  
   *
   * @param {string} name ()  
   * @returns {string}     .   "" .
   */
  getCookie: name => {
    const arg = `${name}=`
    const alen = arg.length
    const clen = document.cookie.length
    let i = 0
    while (i < clen) {
      const j = i + alen
      if (document.cookie.substring(i, j) === arg) {
        return cookieUtil.getCookieVal(j)
      }
      i = document.cookie.indexOf(' ', i) + 1
      if (i === 0) {
        break
      }
    }
    return ''
  },
  /**
   *  
   *
   * @param {string} name ()  
   * @param {string} value ()  
   * @param {string} [expires] ()   
   * @param {string} [path='/'] () path
   * @param {string} [domain=''] () 
   * @param {string} [secure=''] () 
   */
  setCookie: (name, value, expires, path = '/', domain = '', secure = '') => {
    document.cookie = `${name}=${escape(value)
                        }${(expires) ? `; expires=${expires}` : ''
                        }${(path) ? `; path=${path}` : ''
                        }${(domain) ? `; domain=${domain}` : ''
                        }${(secure) ? '; secure' : ''}`
  },
  /**
   *  
   *
   * @param {String} name ()   
   * @param {string} [path='/'] () path
   * @param {string} [domain=''] () 
   */
  deleteCookie: (name, path = '/', domain = '') => {
    if (cookieUtil.getCookie(name)) {
      const pathStr = path ? `; path=${path}` : ''
      const domainStr = domain ? `; domain=${domain}` : ''
      document.cookie = `${name}=${pathStr}${domainStr}; expires=Thu, 01-Jan-70 00:00:01 GMT`
    }
  },
  /**
   *        
   *
   * @param {number} days ()    (  3     3 )
   * @param {number} hours ()    (  2     2 )
   * @param {number} minutes ()    (  30     30 )
   * @returns {string}  
   */
  getExpDate: (days, hours, minutes) => {
    const expDate = new Date()
    if (typeof days === 'number' && typeof hours === 'number' && typeof minutes === 'number') {
      expDate.setDate(expDate.getDate() + Number(days))
      expDate.setHours(expDate.getHours() + Number(hours))
      expDate.setMinutes(expDate.getMinutes() + Number(minutes))
    }
    return expDate.toGMTString()
  },
  /**
   *       
   *
   * @param {object} offset
   * @returns
   */
  getCookieVal: offset => {
    let endstr = document.cookie.indexOf(';', offset)
    if (endstr === -1) {
      endstr = document.cookie.length
    }
    return unescape(document.cookie.substring(offset, endstr))
  }
}

export default cookieUtil
