import CONST from '@/common/constants/framework/framework'
import logUtil from '@frameworks/logUtil'

const errorUtil = {
  /**
   *  Url 
   *
   * @returns {string}  URL
   */
  getCurrentURL () {
    const currentURL = location.href

    return currentURL
  },
  /**
   *  
   *
   * @param {object} vm - View Model 
   * @returns {string}  name
   */
  getComponentName (vm) {
    let componentName = ''

    if (vm) {
      componentName = vm.$options.name
    }

    return componentName
  },
  /**
   *   
   *
   * @param {Error} error -  
   * @param {object} vm - View Model 
   * @returns {ErrorLogParam}
   */
  generateErrorParam (error, vm) {
    const { message, stack } = error
    const currentURL = this.getCurrentURL()
    const componentName = this.getComponentName(vm)

    const params = {
      type: CONST.LOG.MESSAGE.REQUEST.ERROR,
      message,
      trace: stack,
      status: '500',
      page: currentURL,
      view: componentName
    }

    return params
  },
  /**
   *   
   *
   * @param {Error} error -  
   * @param {object} vm - View Model 
   */
  logError (error, vm) {
    const params = this.generateErrorParam(error, vm)

    logUtil.sendRecord(CONST.LOG.TYPE.ERROR, params)
  },
  /**
   *   
   *
   * @param {object} error - error 
   * @param {object} vm - View Model 
   */
  handleError (error, vm) {
    this.logError(error, vm)
  }
}

/**
 *    
 * @typedef {object} ErrorLogParam
 * @property {string} type -   ex) CONST.LOG.MESSAGE.REQUEST.ERROR
 * @property {string} message -  
 * @property {string} trace -  stack
 * @property {string} status -  ( '500' )
 * @property {string} page -    URL
 * @property {string} view -    name
 */

export default errorUtil
