    EVENT_POPUP_LOGIN: 'eventPopupLogin', //  ()
    EVENT_POPUP_REGIST: 'eventPopupRegist', //  ()
    EVENT_POPUP_MAIN: 'eventPopupMain', //  ()
    EVENT_POPUP_DETAIL: 'eventPopupProductDetail', //  ()
    EVENT_POPUP_EXHABIT: 'eventPopupExhabition', //  ()
