import {
  isJsonString
} from '@utils/commonutil/commonUtil'
import cipherUtil from '@frameworks/cipherUtil'
import CONST from '@constants/framework/framework'

//  sessionStorage  
const sessionStorageUtil = {
  /**
   * sessionStorage getItem 
   * @param {String} name ()  
   * @returns {*} sessionStorage   
   */
  get: name => {
    let result = null

    result = sessionStorage.getItem(name)

    if (!result) {
      return result
    }

    // 
    if (CONST.IS_CIPHER_ON_STORAGE) {
      result = cipherUtil.decryptLightly(result)
    }

    if (isJsonString(result)) {
      result = JSON.parse(result)
    }

    return result
  },
  /**
   * sessionStorage setItem 
   * @param {String} name () key (@utils/constants -> COMM_CONST.STORAGE_KEY   )
   * @param {*} val () value
   */
  set: (name, val) => {
    let result

    if (typeof val === 'string') {
      result = val
    } else {
      result = JSON.stringify(val)
    }

    // 
    if (CONST.IS_CIPHER_ON_STORAGE) {
      result = cipherUtil.encryptLightly(result)
    }

    sessionStorage.setItem(name, result)
  },
  /**
   * sessionStorage 
   * @param {String} name ()   
   */
  del: name => {
    sessionStorage.setItem(name, '') // NSSR-9277     

    if (sessionStorage.getItem(name) !== '') {
      sessionStorage.removeItem(name)
    }
  },
  clear: () => {
    sessionStorage.clear()
  }
}

export default sessionStorageUtil
