
import {
  isOsApp
} from '@utils/commonutil/commonUtil'
import COMM_CONST from '@constants/framework/constants'
import cookieUtil from '@frameworks/cookieUtil'
// import nativeUtil from '@frameworks/nativeUtil'
import localStorageUtil from '@frameworks/localStorageUtil'
import sessionStorageUtil from '@frameworks/sessionStorageUtil'
// import loginUtil from '@utils/loginUtil'
/**
 *   (1 )
 *
 */
function onReloadApp () {
  let appReloadCnt = localStorageUtil.get(COMM_CONST.STORAGE_KEY.APP_RELOAD_CNT) || 0
  appReloadCnt = Number(appReloadCnt)
  if (appReloadCnt === 0) {
    sessionStorageUtil.clear()

    if (isOsApp()) {
      // nativeUtil.logOut()
      // loginUtil.logout()
    }

    cookieUtil.setCookie('co_cd', COMM_CONST.getCocd(), null, '/', 'nsmall.com') //  co_cd  

    localStorageUtil.set(COMM_CONST.STORAGE_KEY.APP_RELOAD_CNT, 1)

    document.location.reload()
  } else {
    localStorageUtil.del(COMM_CONST.STORAGE_KEY.APP_RELOAD_CNT)
  }
}

export default onReloadApp
